@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  font-family: "Montserrat", sans-serif;
}

@media (max-width: 1024px) {
  * {
    max-width: 100%;
  }
}

body {
  height: 100%;
}

/* HEADER */

.header {
  background-color: white;
  display: flex;
  flex-direction: column;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 999;
  border-bottom: 1px solid gray;
}

.header .top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  width: 100%;
}

.header .top p {
  margin-left: 20px;
  z-index: 1;
}

.header .top h1 {
  position: absolute;
  width: 100%;
  text-align: center;
}

.header .top h1 a {
  text-decoration: none;
  color: black;
}

.header .top .options ul {
  list-style: none;
  display: flex;
  flex-direction: row;
  margin-right: 20px;
  justify-content: left;
}

.header .top .options li {
  margin-left: 15px;
  z-index: 1;
}

.header .mid {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px 0;
  align-items: center;
}

.header .mid .categories {
  margin-left: -20px;
}

.header .mid .categories ul {
  list-style: none;
  display: flex;
}

.header .mid .categories li {
  margin-right: 20px;
}

.header .mid .categories li a {
  text-decoration: none;
  color: black;
}

.header .mid .categories li a:hover {
  text-decoration: underline;
  font-weight: bolder;
}

.header .mid input {
  margin-right: 10px;
  border-radius: 5px;
  background-color: transparent;
  border: 1px solid black;
  width: 200px;
}

.header .btm ul {
  list-style: none;
  display: flex;
  flex-direction: row;
  margin-left: -20px;
  height: 30px;
  align-items: center;
}

.header .btm li {
  padding-right: 20px;
}

.header .btm li:hover {
  text-decoration: underline;
  font-weight: bolder;
  cursor: pointer;
}

.header-content .top img {
  margin-left: 15px;
  display: none;
}

@media (max-width: 1024px) {
  .header-content .top p,
  .header-content .top .options,
  .header-content .mid,
  .header-content .btm {
    display: none;
  }

  .categories-container {
    margin: 50px auto 0 auto !important;
  }

  h4 {
    font-size: xx-small;
  }

  .header-content .top img {
    display: block;
    z-index: 10000;
  }

  .header,
  .header-content,
  .top h1 {
    width: 100%;
  }
}

/* MEGAMENU */

.megamenu {
  display: flex;
  flex-direction: row;
  margin: 10px 10px;
  padding-bottom: 5px;
  animation: fadeIn 1s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.megamenu .left,
.megamenu .middle {
  width: 30%;
}

.megamenu .right {
  width: 40%;
}

.megamenu img {
  width: 100%;
}

.third-mid {
  margin: 10px 0;
}

.third-mid li {
  list-style: none;
  margin-left: 10px;
}

.megamenu .third-top {
  font-weight: bolder;
  cursor: pointer;
}

.megamenu .third-top:hover {
  text-decoration: underline;
}

.megamenu .third-mid {
  align-items: center;
}

/* WIDE FOOTER */

@media (max-width: 1024px) {
  .footer-wide {
    display: none;
  }
}

.footer-wide {
  background-color: black;
  color: white;
  position: relative;
  bottom: 0;
  left: 0;
  margin-bottom: -30px;
}

.footer-wide .top {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.footer-wide .top h4 {
  font-size: 20px;
  margin: 10px;
}

.footer-wide .top ul {
  list-style: none;
  margin-left: -30px;
  font-size: 15px;
}

.footer-wide .mid {
  margin: 40px 0;
  text-align: center;
}

.footer-wide .btm {
  margin: 20px;
  text-align: center;
  font-size: 13px;
}

.footer-wide .btm .signature {
  padding: 20px;
}

/* SLIM FOOTER */

@media (min-width: 1024px) {
  .footer-slim {
    max-width: 1024px;
    margin: 0 auto;
  }
}

.footer-slim {
  position: relative;
}

.footer-slim .top {
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: center;
  justify-content: space-evenly;
}

.footer-slim .top .about,
.footer-slim .top .reviews {
  justify-content: center;
  align-content: center;
  text-align: center;
  width: 45%;
}

.footer-slim .top h3,
.footer-slim .top .more {
  margin: 20px;
}

.footer-slim .top h3 {
  align-items: baseline;
}

.footer-slim .top .more {
  font-size: 15px;
}

.footer-slim .mid {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.footer-slim .mid .box1,
.footer-slim .mid .box2,
.footer-slim .mid .box3,
.footer-slim .mid .box4 {
  width: 20%;
  margin: 40px 20px;
  text-align: center;
}

.footer-slim .mid h2 {
  font-size: 20px;
  margin: 0 0 15px 0;
}

@media (max-width: 1024px) {
  .footer-slim .mid h2 {
    font-size: small;
  }

  .footer-slim .mid p {
    font-size: smaller;
  }
}

.footer-slim .mid .box1 p,
.footer-slim .mid .box2 p,
.footer-slim .mid .box3 p,
.footer-slim .mid .box4 p {
  align-content: center;
  text-align: center;
}

.footer-slim .btm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.footer-slim .btm .subscribe {
  font-size: 15px;
  margin: 15px 0;
}

.footer-slim .btm .genderpick input {
  margin: 15px 10px;
}

.footer-slim .btm .email input {
  margin: 15px;
}

.footer-slim .btm .email #email-form {
  padding: 0 50px;
  width: 90%;
  text-align: center;
}

.footer-slim .btm .terms p {
  margin: 20px 0 40px 0;
  text-align: center;
  font-size: 14px;
}

.email {
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.okcia {
  margin: auto;
}

/* MOBILE FOOTER */

.footer-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .footer-mobile {
    background-color: black;
    color: white;
    display: flex;
    flex-direction: column;
    text-align: center;
    list-style: none;
    padding: 10px 0px;
  }

  .footer-mobile h4,
  .footer-mobile li,
  .footer-mobile p {
    height: 30px;
    align-items: center;
  }

  .footer-mobile h4 {
    margin: 10px;
    border-bottom: 1px solid rgb(153, 153, 153);
    font-size: medium;
  }

  .footer-mobile li,
  .footer-mobile ul {
    text-align: left;
    list-style: none;
    margin: 0px;
    display: flex;
    flex-direction: column;
  }

  .footer-mobile ul {
    background-color: rgb(34, 34, 34);
    border-bottom: 1px solid rgb(153, 153, 153);
    box-shadow: rgb(0 0 0 / 50%) 0px 0px 15px 0px inset;
    padding: 10px 5%;
  }

  .footer-mobile li:hover {
    cursor: pointer;
    text-decoration: underline;
  }
}

.tereska {
  margin: 200px;
}

.background-blur {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 9999;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.8);
}

.mobile-header {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 50%;
  height: 100%;
  background-color: white;
  z-index: 9999;
  overflow: scroll;
}

.main-categories {
  height: 40px;
  display: flex;
  border-bottom: 1px solid black;
  justify-content: space-evenly;
  margin: 20px 0px;
}

.main-categories p {
  position: relative;
  width: 33%;
  text-align: center;
}

.secondary-categories li {
  list-style: none;
}

.secondary-categories ul {
  -webkit-margin-before: 0px;
          margin-block-start: 0px;
  padding: 0px;
}

.secondary-categories p,
.third-categories p {
  height: 40px;
  margin: 30px 0px;
  text-align: center;
  border-bottom: 2px solid rgb(238, 238, 240);
  text-decoration: none;
  color: black;
}

#link {
  text-decoration: none;
}

button {
  border-radius: 5px;
  font-size: 15px;
  line-height: 44px;
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: white;
  font-weight: bolder;
  margin: 20px auto;
  width: 80%;
}

.third-categories h3 {
  height: 40px;
  margin: 30px 0px;
  padding: 0 30px;
  border-bottom: 2px solid rgb(238, 238, 240);
}

.no-scroll {
  overflow: hidden;
}

@media (min-width: 1024px) {
  .background-blur {
    display: none;
    overflow: hidden;
  }

  .mobile-header {
    display: none;
  }
}

@media (max-width: 700px) {
  .mobile-header {
    width: 70%;
  }

  .background-blur {
    overflow: hidden;
  }
}

@media (max-width: 500px) {
  .mobile-header {
    width: 90%;
  }

  .background-blur {
    overflow: hidden;
  }
}

/* LANGUAGES */

.lang-picker,
.login-background {
  background-color: rgba(0, 0, 0, 0.61);
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 99999999;
  overflow: hidden;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.langs {
  background-color: white;
  color: black;
  padding: 15vh;
  text-align: center;
}

.langs p {
  padding: 20px;
  font-size: larger;
}

.langs a {
  text-decoration: none;
  color: black;
}

.langs a:hover {
  text-decoration: underline;
}

.login-box form {
  display: flex;
  flex-direction: column;
}

.login-box input {
  margin: 20px;
}

.submit {
  background-color: rgb(34, 34, 34);
  border-bottom: 1px solid rgb(153, 153, 153);
  box-shadow: rgb(0 0 0 / 50%) 0px 0px 15px 0px inset;
  padding: 10px 5%;
  color: white;
  font-size: larger;
}

.login-box {
  z-index: 99999999999;
  background-color: white;
  color: black;
  padding: 15vh;
  text-align: center;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: none;
  outline: none;
}

.incorrect-password p {
  color: red;
  font-size: smaller;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#link-third {
  text-decoration: none;
  color: black;
}

/* MAIN PAGE */

.categories-container {
  width: 100% !important;
  margin: 148px auto 0 auto;
  max-height: 600px;
  max-width: 1024px;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: 50% 50%;
  grid-gap: 5px;
  gap: 5px;
}

@media (min-width: 1024px) {
  .categories-container {
    max-width: 1024px;
    max-height: 600px;
    margin: 150px auto 0 auto;
  }
}

@media (max-width: 500px) {
  .categories-container h4 {
    font-size: x-small;
  }
}

.babskie {
  grid-row: 1/3;
  grid-column: 1/2;
}

.casual {
  grid-row: 1/2;
  grid-column: 2/3;
}

.foruglies {
  grid-row: 2/3;
  grid-column: 2/3;
}

.chlopskie {
  grid-row: 1/3;
  grid-column: 3/4;
}

.babskie,
.casual,
.foruglies,
.chlopskie {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: relative;
}

.categories-container figure {
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-color: black;
}

.babskie img,
.casual img,
.foruglies img,
.chlopskie img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
  overflow: hidden;
  transition: 0.3s ease-in-out;
}

.casual img,
.foruglies img {
  max-height: 300px;
}

figure h4 {
  top: 10px;
  left: 10px;
  position: absolute;
  color: white;
  font-weight: bolder;
  z-index: 99;
  background-color: black;
  margin: 5px;
  padding: 5px;
  font-size: medium;
}

.categories-container img:hover {
  opacity: 0.5;
}

@media (min-width: 1024px) {
  .product-container {
    max-width: 1024px;
    margin: auto;
    margin-top: 140px;
  }
}

@media (max-width: 1024px) {
  .product-container {
    max-width: 1024px;
    margin: auto;
    margin-top: 60px;
  }
}

.product-content {
  width: 100%;
}

.product-history {
  display: flex;
  flex-direction: row;
}

.product-history a,
.product-history p {
  margin: 0 5px;
  font-size: small;
}

.product-main {
  display: flex;
  flex-direction: row;
  margin: 10px 20px 20px 20px;
}

.product-main img {
  height: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.product-info {
  width: 100%;
}

.brand,
.name,
.product-categories {
  width: 100%;
  text-align: center;
  margin-bottom: 5px;
}

.product-categories {
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: smaller;
}

.brand {
  letter-spacing: 2px;
}

.price-text {
  text-transform: uppercase;
  margin-top: 20px;
}

.price {
  letter-spacing: 2px;
  margin-top: 5px;
  font-size: larger;
}

.additional-info {
  letter-spacing: 1px;
  margin-top: 5px;
  font-size: small;
}

.options {
  margin-top: 10px;
}

.option-btn {
  margin: 10px 2px 0 2px;
  text-decoration: none;
  list-style: none;
  border: 1px solid #000000;
  display: inline-block;
  cursor: pointer;
  padding: 15px 15px;
  text-decoration: none;
}

.option-btn:hover {
  background-color: #e6e6e6;
}
.option-btn:active {
  position: relative;
  top: 1px;
}

.sizepicker {
  padding: 20px;
  margin-top: 15px;
  width: 80%;
}

@media (max-width: 1024px) {
  .product-container {
    display: flex;
    flex-direction: column;
  }

  .product-main {
    display: flex;
    flex-direction: column;
  }

  .img-container {
    width: 100%;
    justify-content: center;
    margin-bottom: 10px;
  }

  .product-info {
    width: 100%;
    justify-content: center;
    margin-bottom: 20px;
    align-items: center;
  }
}

.img-container {
  width: 100%;
  display: flex;
  flex-direction: row;
}

.img-container .secondary-pics {
  width: 15vh;
  display: flex;
  flex-shrink: 3;
  flex-direction: column;
}

.img-container .secondary-pics img {
  width: 8vh;
  height: auto;
  margin-top: 2vh;
  cursor: pointer;
}

.img-container figure {
  overflow: hidden;
  margin: 0 10px;
}

.img-container img {
  height: 50vh;
  display: block;
  cursor: zoom-in;
}

.img-container .zoom-in img {
  transform: scale(1);
  transition: 0.3s ease-in-out;
}

.img-container .zoom-in:hover img {
  transform: scale(1.5);
}

/* BIGPIC */

.bigpic-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.918);
  z-index: 999;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
}

.bigpic-pic {
  height: 80vh;
  width: 100%;
  display: flex;
  justify-content: center;
}

.bigpic-pic img {
  margin: auto;
  height: 75vh;
  width: auto;
  z-index: 9999;
  cursor: pointer;
}

.bigpic-gallery {
  height: 20vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.bigpic-gallery img {
  height: 15vh;
  margin-left: 10px;
  margin-right: 10px;
  z-index: 9999;
  cursor: pointer;
}

.bigpic-fullscreen {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

#hisotry-link {
  text-decoration: none;
  color: black;
}

#history-link:hover {
  text-decoration: underline;
}

@media (max-width: 1024px) {
  .singleprice {
    margin-top: 10px;
  }

  .product-grid {
    margin: 30px 20px 0 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    grid-gap: 20px;
    gap: 20px;
    text-decoration: none;
  }

  .product-box {
    height: 300px;
    text-align: center;
  }

  .product-box img {
    height: 200px;
    width: 130px;
    object-fit: cover;
  }

  .singlebox {
    width: 25vh;
  }
}

@media (min-width: 1024px) {
  .singleprice {
    margin-top: 10px;
  }

  .product-grid {
    margin: 30px 20px 0 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    grid-gap: 20px;
    gap: 20px;
    text-decoration: none;
  }

  .product-box {
    height: 450px;
    text-align: center;
  }

  .product-box img {
    height: 375px;
    width: 250px;
    object-fit: cover;
  }
}

.categoryName {
  margin-top: 160px;
  width: 100%;
  text-align: center;
}

.product-box h5 {
  text-decoration: none;
}

.link {
  text-decoration: none;
  color: black;
}

.link:hover {
  text-decoration: underline;
}

.product-box img:hover {
  transform: scale(0.98);
}

/* PAGINATION */

.pagination {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 20px;
}

.pagination li {
  list-style: none;
  padding: 10px;
  border: 1px black solid;
  margin: 10px;
}

.pagination li:hover {
  background-color: rgba(128, 128, 128, 0.486);
  transform: scale(0.98);
}

.background-blurr {
  background-color: rgba(0, 0, 0, 0.61);
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 99999999;
  overflow: hidden;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cookie-message {
  background-color: white;
  color: black;
  padding: 15vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.cookie-message h3,
.cookie-message p {
  margin-bottom: 30px;
}

.accept-button {
  padding: 5px 15px;
  background-color: green;
  width: 150px;
  margin: auto;
  font-weight: bolder;
  cursor: pointer;
  text-decoration: none;
  color: black;
}

.accept-button:hover {
  background-color: rgb(0, 58, 0);
  color: white;
  text-decoration: underline;
}

#another-link {
  text-decoration: none;
}

